@charset 'utf-8';

.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    height: 40px;
    margin: 60px 0 60px;
    line-height: normal;
    text-align: center;
    font-size: 0;
}

.tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 40px;
    height: 40px;
    padding-top: 13px;
    padding-bottom: 13px;
    margin-left: 0;
    color: #333;
    font-size: 12px;
    text-decoration: none;
    vertical-align: middle;
    font-family: 'Roboto';
    font-weight: 500;
    line-height: 16px;
}

.tui-pagination .tui-page-btn:hover {
    background-color:var(--footer-background);
    border-radius: 5px;
}

.tui-pagination .tui-prev:hover {
    background-color: var(--white-text-color);
    border-radius: 5px;
}
.tui-pagination .tui-next:hover {
    background-color: var(--white-text-color);
    border-radius: 5px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    color: #fff;
    background: var(--accent-color);
    border-radius: 5px;
    cursor: default;
}

.tui-pagination .tui-is-selected:hover {
    background-color: var(--accent-color);
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
    margin-left: 0;
}

// .tui-pagination .tui-first-child,
// .tui-pagination .tui-prev-is-ellip {

// }

// .tui-pagination .tui-last-child,
// .tui-pagination .tui-next-is-ellip {
// }


.tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid var(--accent-color);
}

.tui-pagination .tui-last-child.tui-is-selected {
    border-right: 1px solid var(--accent-color);
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 40px;
    padding: 0;
    font-size: 0;
    white-space: nowrap;
    font-weight: normal;
    padding: 12px;
    border-radius: 5px;
}

.tui-pagination .tui-first,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip{
 @media screen and (max-width: 450px) {
display: none;}
 }

.tui-pagination .tui-prev,
.tui-pagination .tui-next{
    overflow: hidden;
        height: 40px;
        padding: 0;
        font-size: 0;
        white-space: nowrap;
        font-weight: normal;
        padding: 12px;
        background-color: var(--footer-background);
        border-radius: 5px;
        box-shadow: var(--shadow-page-pagination);
   
}


.tui-pagination .tui-prev-is-ellip {
    border-right: 0
}

.tui-pagination .tui-next-is-ellip {
    border-left: 0
}

.tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
    border-left: 0
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
}
.tui-pagination .tui-ico-prev {
    background:url(/src/images/arrow-left.svg);
        display: inline-block;
        vertical-align: middle;
        font-size: 0px;
}


.tui-pagination .tui-ico-next {
    background: url(/src/images/arrow-right.svg);
     display: inline-block;
    vertical-align: middle;
    font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    padding: 0;
    line-height: 1;
}

// .tui-pagination .tui-prev {

// }

// .tui-pagination .tui-next {
// }

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px
}

.tui-pagination .tui-ico-prev {
    width: 16px;
    height: 16px;
}

.tui-pagination .tui-ico-next {
    width: 16px;
    height: 16px;
}

.tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
    width: 11px;
    height: 2px;
    margin-top: 19px; 
    background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
    background: var(--white-text-color);
    cursor: default;
}

// .tui-pagination .tui-is-disabled .tui-ico-prev {

// }
.tui-pagination .tui-is-disabled .tui-prev {
background-color: var(--white-text-color);
}

.tui-page-btn .tui-is-disabled .tui-prev{
    background-color: var(--white-text-color);
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px;}

// .tui-pagination .tui-is-disabled .tui-ico-next {
//     color: aqua;
    
// }

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px;
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: top;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 8px 0 6px;
    font-size: 12px;
    text-align: center;
    color: #000;
    border: 0;
    outline: none;}

.tui-page-inputs .tui-page-current {
    color: var(--accent-color);
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    vertical-align: top;
}



